
.rjcv-checkbox {
  font-size: 0.32rem;
  position: relative;
  display: inline-block;
  white-space: nowrap;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  color: #999;
}
.rjcv-checkbox .rjcv-checkbox__input {
    white-space: nowrap;
    cursor: pointer;
    outline: none;
    display: inline-block;
    line-height: 1;
    position: relative;
    vertical-align: top;
}
.rjcv-checkbox .rjcv-checkbox__inner {
    display: inline-block;
    position: relative;
    border: 1px solid #ccc;
    border-radius: 2px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 20px;
    height: 20px;
    background-color: #fff;
    z-index: 1;
}
.rjcv-checkbox .rjcv-checkbox__inner::after {
      -webkit-box-sizing: content-box;
              box-sizing: content-box;
      content: "";
      border: 1px solid #fff;
      border-left: 0;
      border-top: 0;
      height: 8px;
      left: 6px;
      position: absolute;
      top: 3px;
      -webkit-transform: rotate(45deg) scaleY(0);
          -ms-transform: rotate(45deg) scaleY(0);
              transform: rotate(45deg) scaleY(0);
      width: 4px;
}
.rjcv-checkbox .rjcv-checkbox__original {
    opacity: 0;
    outline: none;
    position: absolute;
    margin: 0;
    width: 0;
    height: 0;
    left: -999px;
}
.rjcv-checkbox .rjcv-checkbox__label {
    display: inline-block;
    padding-left: 5px;
    line-height: 19px;
    font-size: 16px;
}
.rjcv-checkbox .rjcv-checkbox__input.is-checked .rjcv-checkbox__inner {
    background-color: #ff6ea2;
    border-color: #ff6ea2;
}
.rjcv-checkbox .rjcv-checkbox__input.is-checked .rjcv-checkbox__inner::after {
    -webkit-transform: rotate(45deg) scaleY(1);
        -ms-transform: rotate(45deg) scaleY(1);
            transform: rotate(45deg) scaleY(1);
}
.rjcv-checkbox .rjcv-checkbox__input.is-checked + .rjcv-checkbox__label {
    color: #ff6ea2;
}
